foterFormEl.addEventListener('submit', onJoinSubmit);

function onJoinSubmit(event) {
  event.preventDefault();
  iziToast.show({
    message: '–î—è–∫—É—î–º–æ –∑–∞ —Ä–µ—î—Å—Ç—Ä–∞—Ü—ñ—é!',
    backgroundColor: '#fceee6',
    position: 'topRight',
    close: false,
    messageSize: '18',
    timeout: 2000,
  });
  event.target.reset();
}
